.blob {
    position: block;
}

.canvas {
    width:100%;
    margin-top: -7%;

    @media screen and (max-width: 1200px) {
        margin-top: -10%;
        width: 800px;
    }
    @media screen and (max-width: 600px) {
        width: 400px;
    }
}

.controls {
    background: #3F4656;
    display: flex;
    padding: 20px;
    border-radius: 10px;
    position: relative;
    z-index: 3;
    width:100%;
    max-width:400px;
    box-shadow: 0 4px 20px -1px rgba(18, 22, 33, .7);
    
    @media(max-width: 1200px) {
        margin-top: -4%;
    }
    @media(max-width: 600px) {
        flex-direction: column;
        max-width:60%;
    }
    label {
        color: #CDD9ED;
        font-weight: 500;
        font-size: 14px;
        display: block;
        margin-bottom: 16px;
        @media(max-width: 600px) {
            margin-bottom: 12px;
        }
    }
}

.slider{
    width:100%;
}